@import '../../../../../../styles/settings';

.root {
  width: 100%;
  height: auto;
  background: $primary;
  .container {
    @include flex(flex-start, center);
    flex-wrap: wrap;
    width: 100%;
    .rating {
      width: 100%;
      @include flex(flex-start, flex-start);
      margin: 3rem 0;
      .start {
        @include flex(center, center);
        flex-wrap: wrap;
        p {
          width: 100%;
          text-align: center;
          color: $main-font;
        }
        p:first-of-type {
          font-size: 2.6rem;
          color: $main-btn;
          font-weight: 800;
        }
        p:last-of-type {

        }
        span {
          width: 100%;
          text-align: center;
          color: $main-btn;
          font-size: 1.3rem;
          margin: .4rem 0;
          svg {
            margin-right: .3rem;
          }
        }
        button {
          margin: 1.5rem;
        }
      }
      .ratingChart {
        @include flex(center,center);
        flex-wrap: wrap;
        p {
          text-align: center;
          width: 100%;
          color: $main-btn;
          font-size: 1.3rem;
        }
        span {

        }
      }
    }
    .commentsContainer {
      @include flex(flex-start, flex-start);
      flex-wrap: wrap;
      background: $primary-white;
      padding: 3rem;
      width: 90%;
      margin-left: 4rem;
      h5 {
        width: 100%;
        font-size: 1.6rem;
        color: $second-font;
        border-bottom: 3px solid $main-btn;
        padding-bottom: 1rem;
      }
      .comment {
        @include flex(flex-start, flex-start);
        background: $main-font;
        width: 100%;
        border-bottom: 1px solid rgba(0,0,0,0.2);
        .commentData {
          width: 20%;
          margin: 2rem 0;
          span {
            margin-bottom: .5rem;
            svg {
              font-size: 1.1rem;
              margin-right: .2rem;
            }
          }
          p {
            margin: .5rem 0;
          }
        }
        .commentText {
          width: 80%;
          margin: 2rem 0;
          h3 {
            margin-bottom: 1rem;
            font-size: 1.5rem;
          }
          p {
            font-size: 1.1rem;
          }
        }
      }
    }
  }
}

@media (max-width: 479px) {
  .root {
    .container {
      .rating {
        margin: 1rem 0;
        .start {
          p:first-of-type {
            font-size: 2rem;
          }
          span {
            font-size: 1.2rem;
          }
          button {
            margin: 1.5rem;
          }
        }
        .ratingChart {
          display: none;
        }
      }
      .commentsContainer {
        padding: 1rem;
        margin-left: 1rem;
        h5 {
          font-size: 1.4rem;
        }
        .comment {
          .commentData {
            width: 30%;
            span {
              margin-bottom: .5rem;
              svg {
                font-size: 0.7rem;
              }
            }
          }
          .commentText {
            width: 70%;
            margin: 2rem 0 2rem 1rem;
            h3 {
              margin-bottom: .5rem;
              font-size: 1.4rem;
            }
            p {
              font-size: 1rem;
            }
          }
        }
      }
    }
  }
}
@media (min-width: 480px) and (max-width: 770px) {
  .root {
    .container {
      .rating {
        margin: 1rem 0;
        .start {
          p:first-of-type {
            font-size: 2rem;
          }
          span {
            font-size: 1.2rem;
          }
          button {
            margin: 1.5rem;
          }
        }
        .ratingChart {
          display: none;
        }
      }
      .commentsContainer {
        padding: 1rem;
        margin-left: 1rem;
        h5 {
          font-size: 1.4rem;
        }
        .comment {
          .commentData {
            width: 30%;
            span {
              margin-bottom: .5rem;
              svg {
                font-size: 0.7rem;
              }
            }
          }
          .commentText {
            width: 70%;
            margin: 2rem 0 2rem 1rem;
            h3 {
              margin-bottom: .5rem;
              font-size: 1.4rem;
            }
            p {
              font-size: 1rem;
            }
          }
        }
      }
    }
  }
}
